'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { useParams } from 'next/navigation';

import { Topbar } from '../../../../../components/Topbar';
import { Button } from '../../../../../components/Button';
import { Card } from '../../../../../components/Card';
import Input from '../../../../../components/Input';
import Toggle from '../../../../../components/Toggle';
import Icon from '../../../../../components/Icon';

import createClient from '../../../../../lib/supabaseBrowser';

type WorkoutTypeUI = 'rodagem' | 'progressivo' | 'alternado';
type IntensityUI = 'leve' | 'moderado' | 'forte';

type StudentRow = {
  id: string;
  trainer_id: string;
  name: string;
  email: string | null;
  p1k_sec_per_km: number | null;
};

type BlockUI = {
  id: string;
  distanceKm: string; // string para permitir apagar/editar livremente
  intensity: IntensityUI;
};

function makeId() {
  // Evita depender de crypto.randomUUID em ambientes antigos
  return `${Date.now()}_${Math.random().toString(16).slice(2)}`;
}

function generateShareSlug() {
  // slug curto para URL, sem caracteres especiais
  return `${Math.random().toString(36).slice(2, 10)}${Date.now().toString(36).slice(-4)}`;
}

function onlyDecimalChars(input: string) {
  // permite digitar "2", "2.", "2,5"
  return input.replace(/[^\d.,]/g, '');
}

function parsePtDecimal(input: string): number | null {
  const cleaned = input.trim().replace(',', '.');
  if (!cleaned) return null;
  const n = Number(cleaned);
  if (Number.isNaN(n)) return null;
  return n;
}

function formatKm(n: number) {
  if (!Number.isFinite(n)) return '0';
  const s = n.toFixed(1);
  return s.endsWith('.0') ? String(Math.round(n)) : s;
}

function formatPaceFromSec(secPerKm: number | null | undefined) {
  if (!secPerKm || secPerKm <= 0) return '—';
  const min = Math.floor(secPerKm / 60);
  const sec = secPerKm % 60;
  return `${min}:${String(sec).padStart(2, '0')}`;
}

/**
 * Alguns ambientes têm CHECK CONSTRAINT em `workouts.template_type` e/ou `workouts.status`.
 * Para não “quebrar” nada do que já funciona, tentamos inserir com valores em PT
 * e, se der erro de constraint, tentamos variações comuns (EN/Title Case/UPPER).
 */
function templateTypeCandidates(ui: WorkoutTypeUI): string[] {
  const pt = ui; // rodagem/progressivo/alternado

  const ptTitle =
    ui === 'rodagem' ? 'Rodagem' : ui === 'progressivo' ? 'Progressivo' : 'Alternado';

  const en =
    ui === 'rodagem' ? 'easy' : ui === 'progressivo' ? 'progressive' : 'interval';

  const enUpper = en.toUpperCase();
  const ptUpper = pt.toUpperCase();

  return Array.from(new Set([pt, ptTitle, ptUpper, en, enUpper]));
}

function statusCandidates(): string[] {
  return Array.from(new Set(['draft', 'rascunho', 'DRAFT', 'RASCUNHO']));
}

export default function NewWorkoutPage() {
  const params = useParams();

  const studentId = useMemo(() => {
    const raw = (params as any)?.id;
    if (Array.isArray(raw)) return raw[0] ?? '';
    return raw ?? '';
  }, [params]);

  const supabase = useMemo(() => createClient(), []);

  const [trainerId, setTrainerId] = useState<string>('');
  const [student, setStudent] = useState<StudentRow | null>(null);

  const [loading, setLoading] = useState<boolean>(true);
  const [saving, setSaving] = useState<boolean>(false);
  const [error, setError] = useState<string>('');

  // Form states
  const [title, setTitle] = useState<string>('');
  const [workoutType, setWorkoutType] = useState<WorkoutTypeUI>('rodagem');

  const [includeWarmup, setIncludeWarmup] = useState<boolean>(true);
  const [warmupKm, setWarmupKm] = useState<string>('2');

  const [includeCooldown, setIncludeCooldown] = useState<boolean>(false);
  const [cooldownKm, setCooldownKm] = useState<string>('1');

  const [blocks, setBlocks] = useState<BlockUI[]>([
    { id: makeId(), distanceKm: '5', intensity: 'moderado' },
  ]);

  const titlePlaceholder = useMemo(() => {
    if (workoutType === 'rodagem') return 'Ex.: Rodagem leve';
    if (workoutType === 'progressivo') return 'Ex.: Progressivo';
    return 'Ex.: Alternado';
  }, [workoutType]);

  const totalKm = useMemo(() => {
    const warm = includeWarmup ? (parsePtDecimal(warmupKm) ?? 0) : 0;
    const cool = includeCooldown ? (parsePtDecimal(cooldownKm) ?? 0) : 0;
    const main = blocks.reduce((acc, b) => acc + (parsePtDecimal(b.distanceKm) ?? 0), 0);
    return warm + main + cool;
  }, [includeWarmup, warmupKm, includeCooldown, cooldownKm, blocks]);

  useEffect(() => {
    let cancelled = false;

    async function init() {
      setLoading(true);
      setError('');

      try {
        // 1) Verifica sessão
        const { data: userData, error: userErr } = await supabase.auth.getUser();
        if (userErr) throw userErr;
        const user = userData?.user;
        if (!user) {
          throw new Error('Sessão expirada. Faça login novamente.');
        }
        if (cancelled) return;
        setTrainerId(user.id);

        // 2) Carrega aluno (NUNCA usar .single() aqui para evitar 406)
        const { data: rows, error: stErr } = await supabase
          .from('students')
          .select('id, trainer_id, name, email, p1k_sec_per_km')
          .eq('id', studentId)
          .limit(1);

        if (stErr) throw stErr;

        const row = Array.isArray(rows) ? rows[0] : null;
        if (!row) {
          throw new Error('Aluno não encontrado (ou sem permissão para acessar).');
        }
        if (row.trainer_id !== user.id) {
          throw new Error('Você não tem permissão para acessar este aluno.');
        }

        if (cancelled) return;
        setStudent(row as StudentRow);
      } catch (e: any) {
        if (cancelled) return;
        setStudent(null);
        setError(e?.message ?? 'Erro ao carregar dados.');
      } finally {
        if (!cancelled) setLoading(false);
      }
    }

    if (studentId) init();

    return () => {
      cancelled = true;
    };
  }, [studentId, supabase]);

  function updateBlock(id: string, patch: Partial<BlockUI>) {
    setBlocks((prev) => prev.map((b) => (b.id === id ? { ...b, ...patch } : b)));
  }

  function addBlock() {
    setBlocks((prev) => [
      ...prev,
      { id: makeId(), distanceKm: '1', intensity: 'moderado' },
    ]);
  }

  function removeBlock(id: string) {
    setBlocks((prev) => prev.filter((b) => b.id !== id));
  }

  function validateForm(): string | null {
    if (!student) return 'Aluno não carregado.';
    if (!trainerId) return 'Sessão expirada. Faça login novamente.';

    if (includeWarmup) {
      const n = parsePtDecimal(warmupKm);
      if (n === null) return 'Informe a distância do aquecimento.';
      if (n < 0) return 'A distância do aquecimento não pode ser negativa.';
    }

    if (includeCooldown) {
      const n = parsePtDecimal(cooldownKm);
      if (n === null) return 'Informe a distância do desaquecimento.';
      if (n < 0) return 'A distância do desaquecimento não pode ser negativa.';
    }

    if (!blocks.length) return 'Adicione pelo menos 1 bloco principal.';

    for (const b of blocks) {
      const n = parsePtDecimal(b.distanceKm);
      if (n === null) return 'Preencha a distância de todos os blocos.';
      if (n <= 0) return 'A distância do bloco deve ser maior que 0.';
    }

    return null;
  }

  async function insertWorkoutWithFallbacks(basePayload: any) {
    let lastError: any = null;

    for (const status of statusCandidates()) {
      for (const template_type of templateTypeCandidates(workoutType)) {
        const payload = { ...basePayload, status, template_type };

        const { data, error: insErr } = await supabase
          .from('workouts')
          .insert(payload)
          .select('id, share_slug');

        if (!insErr) {
          const row = Array.isArray(data) ? data[0] : (data as any);
          if (!row?.id) throw new Error('Treino salvo, mas não retornou o ID.');
          return row;
        }

        lastError = insErr;

        // Se não for constraint de status/template_type, não faz sentido insistir em fallback
        const msg = String(insErr?.message ?? '');
        const code = String(insErr?.code ?? '');
        const constraintHint =
          msg.includes('workouts_template_type_check') ||
          msg.includes('workouts_status_check') ||
          code === '23514';

        if (!constraintHint) {
          throw insErr;
        }
      }
    }

    throw lastError ?? new Error('Falha ao salvar treino.');
  }

  function blocksPayload() {
    return blocks.map((b) => ({
      distance_km: parsePtDecimal(b.distanceKm) ?? 0,
      intensity: b.intensity,
    }));
  }

  function computeTotalKm(warm: number, cool: number, bl: any[]) {
    return warm + cool + bl.reduce((acc: number, b: any) => acc + (b.distance_km ?? 0), 0);
  }

  async function handleSaveDraft() {
    setError('');

    const validationError = validateForm();
    if (validationError) {
      setError(validationError);
      return;
    }

    if (!student) return;

    setSaving(true);
    try {
      const warm = includeWarmup ? parsePtDecimal(warmupKm) ?? 0 : 0;
      const cool = includeCooldown ? parsePtDecimal(cooldownKm) ?? 0 : 0;
      const bl = blocksPayload();

      const payload = {
        student_id: student.id,
        trainer_id: trainerId,
        status: 'draft', // será ajustado no fallback se existir check constraint
        template_type: workoutType, // será ajustado no fallback se existir check constraint
        include_warmup: includeWarmup,
        warmup_km: warm,
        include_cooldown: includeCooldown,
        cooldown_km: cool,
        template_params: { ui_workout_type: workoutType },
        blocks: bl,
        total_km: computeTotalKm(warm, cool, bl),
        version: 1,
      };

      await insertWorkoutWithFallbacks(payload);
      setError('');
    } catch (e: any) {
      setError(e?.message ?? 'Erro ao salvar rascunho.');
    } finally {
      setSaving(false);
    }
  }

  async function handleShare() {
    setError('');

    const validationError = validateForm();
    if (validationError) {
      setError(validationError);
      return;
    }

    setSaving(true);
    try {
      const warm = includeWarmup ? parsePtDecimal(warmupKm) ?? 0 : 0;
      const cool = includeCooldown ? parsePtDecimal(cooldownKm) ?? 0 : 0;
      const bl = blocksPayload();

      const basePayload = {
        student_id: student?.id,
        share_slug: generateShareSlug(),
        trainer_id: trainerId,
        status: 'draft',
        template_type: workoutType,
        include_warmup: includeWarmup,
        warmup_km: warm,
        include_cooldown: includeCooldown,
        cooldown_km: cool,
        template_params: { ui_workout_type: workoutType },
        blocks: bl,
        total_km: computeTotalKm(warm, cool, bl),
        version: 1,
      };

      const row = await insertWorkoutWithFallbacks(basePayload);
      const slug = row?.share_slug;

      if (!slug) {
        setError(
          'Treino salvo, mas não foi gerado um link de compartilhamento. Verifique se existe trigger/coluna share_slug no banco.'
        );
        return;
      }

      const url = `${window.location.origin}/w/${slug}`;

      // Web Share API (mobile)
      if ((navigator as any)?.share) {
        await (navigator as any).share({
          title: 'PaceLink - Treino',
          text: 'Treino criado no PaceLink',
          url,
        });
        return;
      }

      // Fallback: copia e abre
      try {
        await navigator.clipboard.writeText(url);
      } catch {
        // ignore
      }
      window.open(url, '_blank', 'noopener,noreferrer');
    } catch (e: any) {
      setError(e?.message ?? 'Erro ao compartilhar.');
    } finally {
      setSaving(false);
    }
  }

  return (
    <div className="flex-1 flex flex-col bg-background-light dark:bg-background-dark">
      {/* P0: voltar no Topbar (mesmo padrão da tela Perfil do Aluno) */}
      <Topbar title="Criar Treino" showBack />

      <div className="max-w-xl w-full mx-auto px-4 pb-28 pt-4 space-y-4">
        {error ? (
          <div className="rounded-xl border border-red-500/40 bg-red-500/10 text-red-200 px-4 py-3">
            {error}
          </div>
        ) : null}

        <Card>
          <div className="text-xs uppercase tracking-wider text-white/60">Aluno</div>
          <div className="text-xl font-semibold mt-1">
            {loading ? 'Carregando…' : student?.name ?? '—'}
          </div>
          <div className="text-sm text-white/70 mt-1">
            Ritmo P1K: {formatPaceFromSec(student?.p1k_sec_per_km)} min/km
          </div>
        </Card>

        <Card>
          <div className="text-lg font-semibold">Título do treino</div>
          <div className="mt-3">
            {/* P0: não preencher automaticamente com o tipo; usar apenas placeholder */}
            <Input
              value={title}
              onChange={(e: any) => setTitle(String(e.target.value ?? ''))}
              placeholder={titlePlaceholder}
              type="text"
            />
          </div>
          <div className="text-sm text-white/60 mt-2">
            Distância total estimada: {formatKm(totalKm)} km
          </div>
        </Card>

        <Card>
          <div className="text-lg font-semibold">Tipo de Treino</div>

          <div className="mt-3 flex gap-2">
            <button
              type="button"
              onClick={() => setWorkoutType('rodagem')}
              className={`px-4 py-2 rounded-full border text-sm font-semibold ${
                workoutType === 'rodagem'
                  ? 'bg-primary text-black border-transparent'
                  : 'bg-transparent text-white/80 border-white/20'
              }`}
            >
              Rodagem
            </button>

            <button
              type="button"
              onClick={() => setWorkoutType('progressivo')}
              className={`px-4 py-2 rounded-full border text-sm font-semibold ${
                workoutType === 'progressivo'
                  ? 'bg-primary text-black border-transparent'
                  : 'bg-transparent text-white/80 border-white/20'
              }`}
            >
              Progressivo
            </button>

            <button
              type="button"
              onClick={() => setWorkoutType('alternado')}
              className={`px-4 py-2 rounded-full border text-sm font-semibold ${
                workoutType === 'alternado'
                  ? 'bg-primary text-black border-transparent'
                  : 'bg-transparent text-white/80 border-white/20'
              }`}
            >
              Alternado
            </button>
          </div>
        </Card>

        <Card>
          <div className="text-lg font-semibold">Estrutura</div>

          <div className="mt-4 flex items-center justify-between">
            <div className="font-semibold">Aquecimento</div>
            <Toggle checked={includeWarmup} onCheckedChange={setIncludeWarmup as any} />
          </div>

          {includeWarmup ? (
            <div className="mt-3 grid grid-cols-[1fr_auto] gap-2 items-end">
              <div>
                <div className="text-xs uppercase tracking-wider text-white/60">Distância</div>
                {/* P0: input destravado (string) */}
                <Input
                  value={warmupKm}
                  onChange={(e: any) => setWarmupKm(onlyDecimalChars(String(e.target.value ?? '')))}
                  onBlur={() => {
                    const n = parsePtDecimal(warmupKm);
                    if (n === null) return;
                    setWarmupKm(String(n));
                  }}
                  inputMode="decimal"
                  placeholder="2"
                  type="text"
                />
              </div>
              <div className="text-white/70 pb-2">km</div>
            </div>
          ) : null}

          <div className="mt-6 flex items-center justify-between">
            <div className="font-semibold">Desaquecimento</div>
            <Toggle checked={includeCooldown} onCheckedChange={setIncludeCooldown as any} />
          </div>

          {includeCooldown ? (
            <div className="mt-3 grid grid-cols-[1fr_auto] gap-2 items-end">
              <div>
                <div className="text-xs uppercase tracking-wider text-white/60">Distância</div>
                {/* P0: input destravado (string) */}
                <Input
                  value={cooldownKm}
                  onChange={(e: any) =>
                    setCooldownKm(onlyDecimalChars(String(e.target.value ?? '')))
                  }
                  onBlur={() => {
                    const n = parsePtDecimal(cooldownKm);
                    if (n === null) return;
                    setCooldownKm(String(n));
                  }}
                  inputMode="decimal"
                  placeholder="1"
                  type="text"
                />
              </div>
              <div className="text-white/70 pb-2">km</div>
            </div>
          ) : null}
        </Card>

        <Card>
          {/* P0: opção de adicionar blocos */}
          <div className="flex items-center justify-between">
            <div className="text-lg font-semibold">Blocos Principais</div>
            <Button variant="secondary" onClick={addBlock}>
              + Adicionar bloco
            </Button>
          </div>

          <div className="mt-4 space-y-4">
            {blocks.map((b, idx) => (
              <div key={b.id} className="rounded-2xl border border-white/10 p-4">
                <div className="flex items-start justify-between gap-3">
                  <div className="text-sm font-semibold text-white/80">Bloco {idx + 1}</div>
                  {blocks.length > 1 ? (
                    <button
                      type="button"
                      onClick={() => removeBlock(b.id)}
                      className="text-white/60 hover:text-white text-sm"
                      aria-label="Remover bloco"
                      title="Remover bloco"
                    >
                      Remover
                    </button>
                  ) : null}
                </div>

                <div className="mt-3 grid grid-cols-[1fr_auto] gap-2 items-end">
                  <div>
                    <div className="text-xs uppercase tracking-wider text-white/60">
                      Distância do trecho
                    </div>
                    {/* P0: input destravado (string) */}
                    <Input
                      value={b.distanceKm}
                      onChange={(e: any) =>
                        updateBlock(b.id, {
                          distanceKm: onlyDecimalChars(String(e.target.value ?? '')),
                        })
                      }
                      onBlur={() => {
                        const n = parsePtDecimal(b.distanceKm);
                        if (n === null) return;
                        updateBlock(b.id, { distanceKm: String(n) });
                      }}
                      inputMode="decimal"
                      placeholder="1"
                      type="text"
                    />
                  </div>
                  <div className="text-white/70 pb-2">km</div>
                </div>

                <div className="mt-4">
                  <div className="text-xs uppercase tracking-wider text-white/60 mb-2">
                    Intensidade
                  </div>

                  {/* P1: grid para não cortar "Forte" no celular */}
                  <div className="grid grid-cols-3 gap-2">
                    {(['leve', 'moderado', 'forte'] as IntensityUI[]).map((opt) => (
                      <button
                        key={opt}
                        type="button"
                        onClick={() => updateBlock(b.id, { intensity: opt })}
                        className={`px-3 py-2 rounded-full border text-sm font-semibold ${
                          b.intensity === opt
                            ? 'bg-primary text-black border-transparent'
                            : 'bg-transparent text-white/80 border-white/20'
                        }`}
                      >
                        {opt === 'leve' ? 'Leve' : opt === 'moderado' ? 'Moderado' : 'Forte'}
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </Card>
      </div>

      {/* Bottom actions */}
      <div className="fixed bottom-0 left-0 right-0 p-4 bg-black/30 backdrop-blur border-t border-white/10">
        <div className="max-w-xl w-full mx-auto grid grid-cols-2 gap-3">
          {/* P0: salvar rascunho sem estourar constraint (fallback de template_type/status) */}
          <Button variant="secondary" onClick={handleSaveDraft} disabled={saving || loading}>
            Salvar Rascunho
          </Button>

          <Button onClick={handleShare} disabled={saving || loading}>
            <span className="inline-flex items-center gap-2">
              <Icon name="share" size={18} />
              Compartilhar
            </span>
          </Button>
        </div>
      </div>
    </div>
  );
}
